<template>
  <section class="hero">
    <div class="has-background-white">
      <div class="container has-text-left">
        <h2 class="title">
          <span v-html="dealer.nombre"></span>
        </h2>
      </div>
    </div>
    <div class="hero-body">
      <div class="container">
        <div class="columns card">
          <div class="column dealer-image">
            <div class="container has-text-left has-background-image" :style="'background-image:url('+dealer.imagen+')'"></div>
          </div>
          <div class="column has-text-left">
            <!--pre v-html="dealer"></pre-->
            <h1 class="is-size-3 has-text-weight-semibold" v-html="dealer.nombre"></h1>
            <p><strong>Direccin:</strong> <span v-html="dealer.direccion"></span></p>
            <p><strong>Telfono:</strong> <span v-html="dealer.telefono"></span></p>
            <p><strong>Horarios:</strong> {{ dealer.horarios | cs }} </p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>

import axios from 'axios';
import moment from 'moment';
import helper from '@/components/Helper';

export default {
  name: 'Dealer',
  mounted: function(){
    if(!this.dealer.length) {
      var that = this
      axios.get( `/static/dummy/mercedes-dealers.json`)
        .then(res => {
          _.map(res.data, function(value, key) {
            if(value.slug === that.$route.params.slug){
              that.dealer = value
            }
          });
        }).catch(function(error) {
          console.log(error)
        })      
    }
  },
  data () {
    return {
      dealer:[],
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>



